import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
} from "react-router-dom";
import { Button } from "react-bootstrap";
import * as Constants from "../../Constants/Constants";

class SingleMeme extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
    };
  }

  componentDidMount() {
    fetch(
      Constants.BACKEND_SERVER_HOST +
        Constants.GET_RANDOM_MEME
    )
      .then((res) => res.json ())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            meme: result,
          });
        },
        (error) => {
          this.setState({
            error,
            isLoaded: true,
            next: null,
          });
        }
      );
  }

  refreshCallback = () => {
    this.componentDidMount();
  };

  render = () => {
    const { error, isLoaded, meme } = this.state;
    var memes_to_show = "";
    if (isLoaded && error){
      memes_to_show = "Error. Unable to Load"
    }
    else if (isLoaded){
      if (meme && meme.image){
        memes_to_show = false;
      }
      else{
        memes_to_show = "No Memes to show"
      }

    }
    else{
      memes_to_show = "Loading..."
    }
  return (
    <div className="home-container" style={{display: "flex", flexDirection: "column", padding:"0 5% 5% 5%"}}>
      <span style={{textAlign:"end", fontSize:"18px", marginTop:"50px"}}></span>
      <Link style={{textAlign:"center"}}>
        <Button onClick={this.refreshCallback} variant="outline-blue" style={{textAlign:"center", width:"max-content", fontSize:"24px", fontWeight:"500", fontFamily:"Roboto," +
              " sans-serif"}}>
          Portal to Random Meme
        </Button>
      </Link>
      {memes_to_show ? (memes_to_show) :
          <div style={{padding:"10px", width:"100%", display:"flex", flexDirection:"column"}}>
            <img style={{alignSelf:"center", height:"500px", width:"auto", maxWidth:"80%", flex:1}} src={meme.image} alt="Image"/>
            <span style={{textAlign:"center", fontSize:"18px", marginTop:"20px", flex:1}}>{meme.title}</span>
          </div>
      }
    </div>
  );
  };
}

export default SingleMeme;
