import "./Home.css";
import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
} from "react-router-dom";
import { Button } from "react-bootstrap";
import * as Constants from "../../Constants/Constants";
import {GET_MEME_LIST, SERVER_HOST} from "../../Constants/Constants";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      memes: [],
      urlQueryParams: null,
      pageCount: 0,
      next: null,
      listStyle: "grid",
      show_filter: false,
      filter_to_show: "year"
    };
  }

  componentDidMount() {
    let url_query_params = this.state.urlQueryParams;
    if (!url_query_params) {
      if (this.props.location){
        url_query_params = new URLSearchParams(this.props.location.search);
      }
      else{
        url_query_params = new URLSearchParams();
      }
    } else {
      window.history.pushState(
        { html: window.html, pageTitle: window.pageTitle },
        "",
        window.location.origin +
          window.location.pathname +
          "?" +
          url_query_params.toString()
      );
    }
    fetch(
      Constants.BACKEND_SERVER_HOST +
        Constants.GET_MEME_LIST +
        "?" +
        url_query_params.toString()
    )
      .then((res) => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            memes: result,
            next: result.next,
            urlQueryParams: url_query_params,
          });
        },
        (error) => {
          this.setState({
            error,
            isLoaded: true,
            next: null,
            urlQueryParams: url_query_params,
          });
        }
      );
  }

  refreshCallback = () => {
    this.componentDidMount();
  };

  render = () => {
    const { error, isLoaded, memes, urlQueryParams } = this.state;
    var memes_to_show = "";
    if (isLoaded && error){
      memes_to_show = "Error. Unable to Load"
    }
    else if (isLoaded){
      if (memes && memes.count>0){
        memes_to_show = false;
      }
      else{
        memes_to_show = "No Memes to show"
      }

    }
    else{
      memes_to_show = "Loading..."
    }
  return (
    <div className="home-container" style={{display: "flex", flexDirection: "column", padding:"0 5% 5% 5%"}}>
      <span style={{textAlign:"end", fontSize:"18px", marginTop:"50px"}}></span>
      <Link to={"/" + Constants.RANDOM_MEME}>
        <Button variant="outline-blue" style={{textAlign:"center", width:"max-content", fontSize:"24px", fontWeight:"500", fontFamily:"Roboto," +
              " sans-serif"}}>
          Portal to Random Meme
        </Button>
      </Link>
      <span style={{textAlign:"center", fontSize:"30px", fontWeight:"500", fontFamily:"Roboto, sans-serif"}}>And Here's a list</span>
      {memes_to_show ? (memes_to_show) :
          <div style={{padding:"10px"}}>
            {memes.results.map((key, i) => (
            <div key={{i}}>
              <img style={{width:"100%"}} src={key.image} alt="Image"/>
            </div>
        ))}
          </div>
      }
    </div>
  );
  };
}

export default Home;
